$(function(){
  
  /* ==================================== 초기UI 실행 호출함수 호출  ==================================== */
  window.pubUI = new PubUI();
  window.pubUI.gnb_footer();    // 높이 설정
  ///window.pubUI.base();       // 기본실행
  window.pubUI.forms();         // 폼요소
  window.pubUI.hartMotion();    // 하트 모션
  window.pubUI.scrollTop();     // 스크롤탑
  window.pubUI.mobileBack();    // 모바일 뒤로가기
  window.pubUI.append_script(); // 스크립트 애드
  

  /* 리싸이즈 공통 */
  $(window).on("resize", function(){
    responsiveDevices();        // width  : pc, mobile 체크    
    window.pubUI.gnb_footer();
  });
  responsiveDevices();
  window.pubUI.gnb_footer();

});

/* ==================================== 초기UI 실행 함수 호출  ==================================== */
function PubUI() {
  var _this = this;

  /* 해더 높이 자동 */
  _this.gnb_footer = function() {
    var _topH = $("header.header").innerHeight();
    var _footerH = $("footer.footer").innerHeight();
    gnb_footer_resize(_topH, _footerH);
  }

  _this.base = function() {
    var vh = window.innerHeight * 0.01
    document.documentElement.style.setProperty('--vh', `${vh}px`)
    window.addEventListener('resize', () => {
      var vh = window.innerHeight * 0.01
      document.documentElement.style.setProperty('--vh', `${vh}px`)
    })
  }

  _this.forms = function() {
    
    /* input del */
    function inputdel_fn($this) {
      ///console.log('$this.val()', $this.val());
      var _noneDisable = !$this.prop("disabled");
      if( $this.val() != "" && _noneDisable){
          $this.siblings("button").css({display:"block"});
          $this.siblings("button").one("click", function(){
              $this.val("");
              $this.siblings("button").css({display:"none"});
          });
      }else{
          $this.siblings("button").css({display:"none"});
      }
    }
    $("body").on("focusout", ".input_del input", function(){
        inputdel_fn( $(this) );
    });
    $("body .input_del input").each(function($i){
        inputdel_fn( $(this) );
    });

    /* select - box */
    $(".select2Basic").select2({            
      minimumResultsForSearch: Infinity,
      /*theme: "basic"*/
    });
    $(".select2Basic42").select2({            
      minimumResultsForSearch: Infinity,
      /*theme: "basic"*/
    });
    $(".select2Basic50").select2({            
      minimumResultsForSearch: Infinity,
      /*theme: "basic"*/
    });
    $(".select2Basic_aa").select2({            
      minimumResultsForSearch: Infinity,
      dropdownCssClass : 'select_none_line',
      /*theme: "basic"*/
    });

    $(".select2Basic56_line").select2({            
      minimumResultsForSearch: Infinity,
      /*theme: "basic"*/
    });

  }

  _this.hartMotion = function() {    
    https://www.npmjs.com/package/mo-js
    /* var _hartMotion = new mojs.Burst({ left: 0, top: 0, count: 6,
      className : "mojs_hart",
			radius:   { 5: 20 },
			children: { 
				easing: 'cubic.out',
				fill:   "#4DADA7"  ,
    		duration: 500,
			}
		});  */
		$('.js-work_heart').on('click', function($e) {
			$e.preventDefault();
			$(this).toggleClass('on');
			///if( $(this).hasClass("on") )
      //console.log( $e.pageX , " :: ", $(this).offset().top );
			//_hartMotion.tune({ x: $e.pageX, y: $e.pageY }).generate().replay();
		})
  }

  _this.scrollTop = function() {    
    $(".js-scroll_top").on("click", function($e){
      $e.preventDefault();
      var _position = 0 ///$(selector).offset();
      $('html, body' ).animate( { scrollTop: _position }, 370 );
    })
  }

  _this.mobileBack = function() {    
    $(".js-history_back").click(function(){
			window.history.back();
		})
  }

  


  /* 초기실행 스크립트 */
  _this.append_script = function() {
    ///$("body").append( $(".js-append-script") );
  }

  
}

/* ==================================== 초기실행 함수 호출  ==================================== */
var _szTB  = 1439;   //
var _szMB  = 1023;   //
var _szMS  = 760;    //
/* 호출 */
function responsiveDevices(){ 
    var _change_width = window.innerWidth; //  $(window).innerWidth(); //
    var _temp_width;  
    //console.log(_change_width)
    if( _change_width > _szTB){
       _temp_width = "is_pc";
    }else if(_change_width > _szMB){
       _temp_width = "is_pc";
    }else {
      _temp_width = "is_mb";
    };
    if (!$('body').hasClass(_temp_width)) {
      $("body").removeClass("is_pc is_tb is_mb is_ms");
      $("body").addClass(_temp_width);
      $("body").attr( "data-device" , _temp_width);
      console.log("===============responsiveDevices=====================")
    }
    return _temp_width
}


/* PC,MB images resize */
