/* ========== 바디 높이 조절 ========== */
function gnb_footer_resize($h, $f){
  /*  $('.main-contents') */
  var _h = $("header.header").innerHeight();
  var _f = $("footer.footer").innerHeight();
  if($h){
    $("#contents").css({ "padding-top" : $h ,"padding-bottom" : $f });
    $("footer.footer").css({ "margin-top" : -($f)  });
  }else{
    $("#contents").css({ "padding-top" : _h, "padding-bottom" : _f});
    $("footer.footer").css({ "margin-top" : -(_f)  });
  }
}



function trpScrollTop($selector, $add_gap){
  var _position = $($selector).offset().top - ($("header.header").innerHeight() + $add_gap);
  $( 'html, body' ).animate( { scrollTop: _position }, 300 );
};